<template>
    <div>

        <header id="navigation">
            <div class="wr" >
                <scrollactive class="my-nav">
                <a href="#navigation" class="logo scrollactive-item" ></a>
                <div class="row-2">

                    <nav class="active" >
                        <a href="#resources" class="scrollactive-item">About</a>
                        <a href="#features" class="scrollactive-item">Features</a>
                        <a href="#plans" class="scrollactive-item">Plans</a>
                        <a href="#downloads" class="scrollactive-item">Downloads</a>
                    </nav>


                    <span class="topicon bars"><i class="fas fa-bars"></i></span>
                    <span class="topicon search" ><i class="fas fa-search"></i></span>
                    <div class="search_block active_search" >
                        <input type="search" placeholder="search...">
                    </div>


                </div>
            </scrollactive>

            </div>
        </header>


    </div>
</template>

<script>
    export default {


        name: "header_block",


    }
</script>

<style scoped lang="scss">
    @import "../../assets/css/config";

    header {
        background-color: #0291da;
        position: fixed;
        top:0;
        width: 100%;
        transition: all ease 0.5s;
        z-index: 99;

        &>.wr {
            height:90px;
            display: flex;
            position: relative;
            justify-content: space-between;
            align-items: center;
            z-index:99;

            &>.my-nav {
                height:90px;
                display: flex;
                position: relative;
                justify-content: space-between;
                align-items: center;
                z-index:99;
                width: 100%;


                & > .logo {
                    display: block;
                    width: 125px;
                    height: 35px;
                    margin-left: 1px;
                    background: url("../../assets/images/ENYO_logo.png") no-repeat center / auto;
                }

                & > .row-2 {
                    display: flex;
                    justify-content: flex-end;

                    & > nav {
                        position: relative;
                        display: flex;
                        justify-content: flex-end;
                        align-content: center;

                        @include media($medium) {
                            flex-direction: column;
                            position: absolute;
                            width: 100%;
                            top: 90px;
                            left: 0;
                            background-color: #3bbdff;
                            box-shadow: 0px 5px 20px -5px rgba(0,0,0,1);


                            &.active {
                                display: none;

                            }
                        }

                        & > a {
                            @include media($medium) {
                                font-size: 2em;
                                width: 100%;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                padding: 40px 0;
                                text-transform: uppercase;
                                border-bottom: 3px solid #0393dd;

                            }

                            height: 15px;
                            color: white;
                            font-size: 18px;
                            font-weight: 700;
                            text-decoration: none;
                            margin-right: 20px;
                            transition: all ease 0.2s;

                            &:hover, &.act {
                                color: #25365d;
                            }
                        }

                    }

                    & > .topicon {
                        display: inline-block;
                        color: #ffffff;
                        font-size: 20px;
                        cursor: pointer;
                        margin-right: 20px;

                        &:last-of-type {
                            margin-right: 0;
                        }
                    }
                    & > .bars {
                        display: none;
                        @include media($medium) {
                            display: inline-block;
                        }

                    }
                    & > .search_block {
                        position: absolute;
                        right: 35px;
                        top: 26px;
                        width: 500px;

                        &.active_search {
                            display: none;
                        }

                        @include media($medium) {
                            width: 250px;
                        }

                        & > input {
                            float: right;
                            width: 100%;
                            outline: none;
                            background-color: #fff;
                            height: 35px;
                            padding: 5px 15px 5px 15px;
                            border-radius: 20px;
                            border: 0;
                            font-family: "Open Sans";
                            box-shadow: 0px 0px 10px 0px rgba(37, 54, 93, 0.75);
                        }

                    }

                }
            }
        }


    }
</style>


