<template>
    <div>

        <section id="downloads">
            <div class="wr">
                <h2>DOWNLOAD THIS TEMPLATE FOR FREE</h2>
                <span>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aperiam aspernatur dicta dignissimos enim labore natus, nisi numquam sequi? Consectetur deserunt fugiat iste praesentium sint temporibus.</span>
                <button>
                    <i class="fas fa-download"></i> Download
                </button>
            </div>
        </section>


    </div>
</template>

<script>
    export default {
        name: "downloads_block"
    }
</script>

<style scoped lang="scss">
@import "../../assets/css/config";
#downloads {
    &>.wr {
        padding: 70px 0 50px 0;
        display: flex;
        flex-direction: column;
        justify-content:space-around;
        align-items: center;

        &>h2 {
            padding-bottom: 50px;
            text-align: center;
            font-family: Bebas;
        }
        &>span {
            font-size: 16px;
            font-weight: 400;
            padding-bottom: 50px;
            text-align: center;
        }



        &>button {
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            width: 217px;
            height: 56px;
            background-color: #25365d;
            border-radius: 28px;
            border:0;
            color:#ffffff;
            font-size: 18px;
            font-weight: 700;
            line-height: 42px;
            transition: all ease 0.3s;
            &:hover {
                background-color: #0b193b;
            }

            &>i {
                width: 17px;
                height: 17px;
                opacity: 0.6;
                color: #bbd8ff;
                margin-right: 10px;
            }


        }
    }
}
</style>


