<template>
    <div>

        <section class="description" id="features">
            <div class="wr">
                <h2>FEATURES</h2>
                <ul>
                    <li class="move-left" v-in-viewport.once><i class="fas fa-desktop"></i>
                        <span><h4>HIGH RESOLUTION</h4>
                       Lorem ipsum dolor sit amet, consectetur adipisicing elit. Magnam, voluptatem.</span></li>
                    <li class="move-center" v-in-viewport.once><i class="fas fa-camera"></i>
                        <span><h4>PHOTOGRAPHY</h4>
                       Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aliquid autem doloribus excepturi molestiae molestias neque, quae quas.</span></li>
                    <li class="move-right" v-in-viewport.once><i class="fas fa-edit"></i>
                        <span><h4>EDITABLE LAYERS</h4>
                       Lorem ipsum dolor sit amet, consectetur adipisicing elit. Lorem ipsum dolor sit amet, consectetur adipisicing elit.</span></li>
                    <li class="move-left" v-in-viewport.once><i class="fas fa-mobile-alt"></i>
                        <span><h4>24/7 SUPPORT</h4>
                       Lorem ipsum dolor sit amet, consectetur adipisicing elit. A ab cupiditate delectus dignissimos eligendi non obcaecati porro quam.</span></li>
                    <li class="move-center" v-in-viewport.once><i class="fas fa-magic"></i>
                        <span><h4>POWERFULL THEME</h4>
                       Lorem ipsum dolor sit amet.</span></li>
                    <li class="move-right" v-in-viewport.once><i class="fas fa-comments"></i>
                        <span><h4>TTV</h4>
                      Lorem ipsum dolor sit amet, consectetur adipisicing elit. Corporis cupiditate debitis eaque id labore.</span></li>
                </ul>


            </div>
        </section>


    </div>
</template>

<script>
    export default {
        name: "description_block"
    }
</script>

<style scoped lang="scss">
@import "../../assets/css/config";

.description {
    padding: 40px 0;
    background-color: #f7f9fb;

    &>.wr {
        &>h2 {
            margin: 30px auto;
            font-family: Bebas;
            text-align: center;
        }
        &>ul {
            list-style: none;
            margin: 0;
            padding: 0;

            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(360px, 1fr));
            grid-gap: 10px;



            &>.move-left {
                opacity: 0;
                transform: translate(-50px, 0);
                transition: all 0.5s;
                transition-delay: 0.4s;
                @include media($medium) {
                    transform: translate(0, 50px);
                }
            }
            &>.move-center {
                opacity: 0;
                transform: translate(0, 50px);
                transition: all 0.5s;
                transition-delay: 0.2s;
                @include media($medium) {
                    transform: translate(0, 50px);
                    transition-delay: 0.4s;
                }
            }
            &>.move-right {
                opacity: 0;
                transform: translate(50px, 0);
                transition: all 0.5s;
                transition-delay: 0.4s;
                @include media($medium) {
                    transform: translate(0, 50px);
                }
            }

            &>.move-left.in-viewport, .move-right.in-viewport, .move-center.in-viewport {

                transform: translate(0, 0);
                 opacity: 1;

            }


            &>li {
                display: flex;

                padding: 10px 5px;
                box-sizing: border-box;

                width:100%;
                height: 150px;
                &>i {
                    color:#25365d;
                    font-size:40px;
                    margin-right: 20px;
                }
                &>span {

                    color: #5a5a5a;
                    font-family: "Open Sans";
                    font-size: 16px;
                    font-weight: 400;
                    line-height: 24px;

                    &>h4 {
                        margin: 0;
                        padding: 0;
                        color: #25365d;
                        font-size: 22px;
                        font-weight: 600;
                    }
                }
            }
        }
    }

}
</style>


