<template>
    <div>
        <main>
        <header_block />
        <about_block />
        <resources_block />
        <description_block />
        <plans_block />
        <downloads_block />
        <footer_block />
        </main>

    </div>
</template>

<script>
    import header_block from '../components/blocks/header_block';
    import about_block from '../components/blocks/about_block';
    import resources_block from '../components/blocks/resources_block';
    import description_block from "./blocks/description_block";
    import Plans_block from "./blocks/plans_block";
    import Downloads_block from "./blocks/downloads_block";
    import Footer_block from "./blocks/footer_block";


    export default {

        name: "main_block",
        components: {
            Footer_block,
            Downloads_block, Plans_block, header_block, about_block, resources_block, description_block}
    }
</script>

<style scoped lang="scss">
    main {
        min-width: 100%;

    }
</style>


