<template>
    <div>

        <section class="resources">
        <div id="resources"></div>
            <div class="wr">
                <h1>FREE RESOURCES</h1>
                <h2>JUST DOWNLOAD<nobr/>&nbsp;IT</h2>
                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. </p>
                <ul>
                    <li><i class="fas fa-check-circle"></i>Duis aute irure dolor in reprehenderit in voluptate. </li>
                    <li><i class="fas fa-check-circle"></i>Excepteur sint occaecat cupidatat non proident. </li>
                    <li><i class="fas fa-check-circle"></i>Sed ut perspiciatis unde omnis iste natus error sit voluptatem. </li>
                </ul>
                <button>Read More</button>
            </div>

        </section>
    </div>
</template>

<script>
    export default {
        name: "resources_block"
    }
</script>

<style scoped lang="scss">
@import "../../assets/css/config";
.resources {

    position: relative;
    width: 100%;
    background: url("../../assets/images/header_mountain.png") no-repeat top / cover;


&>#resources {
    position: relative;
    top: 200px;
}

    &>.wr:before {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        display: block;
        z-index:-1;
        width: 877px;
        height: 452px;
        background: url("../../assets/images/Macbook.png") no-repeat bottom right / contain;

        @include media($big){
            background: none;
        }
    }

    &>.wr {
        position: relative;
        z-index:2;
        padding-top: 400px;
        margin-bottom: 50px;
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;

        @include media($big){
            padding-top: 250px;
            align-items: center;

        }

        &>h1 {
            display: block;
            width: 100%;
            text-align: center;
            position: absolute;
            bottom: 30px;
            z-index: -2;
            text-align: center;
            color: white;
            font-family: Bebas;
            font-size: 220px;
            font-weight: 400;

            @include media($big){

                position: absolute;
                left: 0;
                bottom:50px;
                font-size: 100px;
            }


        }
        &>h2 {
            font-family: "Open Sans";

            @include media($big){
                text-align: center;
            }
        }
        &>p {
            max-width: 700px;
            font-family: "Open Sans";
            font-size: 16px;
            font-weight: 600;
            line-height: 32px;
            margin-bottom: 25px;
            @include media($big){
                text-align: center;
            }

        }
        &>ul {
            list-style: none;
            padding: 0;
            margin: 0;

            &>li {
                color: #93aac9;
                font-family: "Open Sans";
                font-size: 16px;
                font-weight: 400;
                line-height: 32px;

                &>i {
                    color:#ff3571;
                    margin-right: 15px;
                }

            }
        }
        &>button {
            margin-top: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            width: 215px;
            height: 55px;
            background-color: #25365d;
            border-radius: 28px;
            border:0;
            color:#ffffff;
            font-size: 18px;
            font-weight: 700;
            transition: all ease 0.3s;
            &:hover {
                background-color: #0b193b;
            }
        }



    }
}
</style>


