<template>
    <div>

        <section id="plans">
            <div class="wr">

                <h2>PLANS</h2>
                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. </p>
                <ul>
                    <li class="move-left" v-in-viewport.once>
                        <h5>Basic Plan</h5>
                        <span><strong>25</strong></span>
                        <span>Lorem ipsum dolor sit amet.</span></li>
                    <li class="move-center" v-in-viewport.once>
                        <h5>Professional Plan</h5>
                        <span><strong>50</strong></span><span>Lorem ipsum dolor sit amet, consectetur adipisicing.</span></li>
                    <li class="move-right" v-in-viewport.once>
                        <h5>Ultra Plan</h5>
                        <span><strong>75</strong></span><span>Lorem ipsum dolor sit.</span></li>
                </ul>
                <div class="icon"><i class="fas fa-question-circle"></i>
                    <h5>GOT questionS?</h5>
                    <span>Contact our customer support team if You have any further questions.<br/>
   We are here to help You out!</span></div>
                <div class="telephones">
                    <span><i class="fas fa-envelope-square"></i><a href="#">support@free.com</a></span>
                    <span><i class="fas fa-phone-square"></i>+48 504 176 933</span>
                </div>

            </div>
        </section>


    </div>
</template>

<script>
    export default {
        name: "plans_block"
    }
</script>

<style scoped lang="scss">
@import "../../assets/css/config";

#plans {
    background-color: #f7f9fb;
    &>.wr {
        padding: 40px 0;
        display: flex;
        flex-direction: column;
        &>h2 {
            margin: 30px auto;
            font-family: Bebas;
        }
        &>p {
            text-align: center;
            color: #5a5a5a;
            font-family: "Open Sans";
            font-size: 16px;
            font-weight: 400;
            line-height: 32px;
            margin-bottom: 50px;
        }
        &>ul {
            list-style: none;
            margin: 0;
            padding: 0;
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(360px, 1fr));
            grid-gap: 30px;



            &>.move-left {
                opacity: 0;
                transform: translate(-50px, 0);
                transition: all 0.5s;
                transition-delay: 0.4s;
                @include media($medium) {
                    transform: translate(0, 50px);
                }
            }
            &>.move-center {
                opacity: 0;
                transform: translate(0, 50px);
                transition: all 0.5s;
                transition-delay: 0.2s;
                @include media($medium) {
                    transform: translate(0, 50px);
                    transition-delay: 0.4s;
                }
            }
            &>.move-right {
                opacity: 0;
                transform: translate(50px, 0);
                transition: all 0.5s;
                transition-delay: 0.4s;
                @include media($medium) {
                    transform: translate(0, 50px);
                }
            }

            &>.move-left.in-viewport, .move-right.in-viewport, .move-center.in-viewport {

                transform: translate(0, 0);
                opacity: 1;

            }

            &>li {
                background-color: #fff;
                height: 285px;
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.2);
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                align-items: center;
                padding: 20px 20px;
                box-sizing: border-box;

                &>h5 {
                    margin: 0;
                    padding: 0;
                    color: #5a5a5a;
                    font-family: "Open Sans";
                    font-size: 18px;
                    font-weight: 400;
                    text-transform: uppercase;

                }

                &>span {
                    color: #5a5a5a;
                    font-family: "Open Sans";
                    font-size: 14px;
                    font-weight: 400;
                    text-align: center;
                    &>strong {
                        color: #25365d;
                        font-family: "Open Sans";
                        font-size: 72px;
                        font-weight: 700;


                        &::before {
                            content:'$';
                            font-size: 40px;
                            font-weight: 700;

                        }

                        &::after {
                            content: "/mon";
                            color: #5a5a5a;
                            font-size: 22px;
                            font-weight: 400;
                        }
                    }

                }

            }
        }

        &>.icon {
            margin: 0 auto;
            text-align: center;
            font-size: 16px;
            font-weight: 400;
            line-height: 32px;
            &>i {
                font-size:48px;
                color: #25365d;
                padding-top: 50px;

            }
            &>h5 {
                text-transform: uppercase;
                color: #25365d;
                font-size: 22px;
                font-weight: 400;
                margin: 0;
                padding: 20px 0;
            }

        }
        &>.telephones {
            display: flex;
            justify-content: center;

            &>span {
                margin-top: 50px;
                color: #25365d;
                font-size: 22px;
                font-weight: 700;
                margin-right: 50px;
                &:last-of-type {
                    margin-right: 0;
                }

                &>a {
                    text-decoration: none;
                    color: #25365d;
                }

                &>i {
                    font-size:28px;
                    color: #25365d;
                    position: relative;
                    bottom: -4px;
                    margin-right: 10px;

                }
            }
        }
    }
}

</style>


