<template>
    <div>
        <section id="about">
            <div class="wr">
                <h1>FREE PSD WEB TEMPLATE</h1>
                <h3>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. </h3>
                <button>
                    <i class="fas fa-download"></i> Download
                </button>


            </div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "about_block"
    }
</script>

<style scoped lang="scss">
    @import "../../assets/css/config";
    #about {
        min-width: 100%;
        background-color: #0291da;
        &>.wr {
            margin-top: 90px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;



            &>h1 {
                margin: 150px 0 0 0;

                color:white;
                font-family: Bebas;
                letter-spacing: 3pt;
                font-size: 91px;
                font-weight: 400;
                text-align: center;
            }
            &>h3 {
                margin: 0;
                max-width: 815px;
                padding-bottom: 50px;
                color: #bbd8ff;
                font-family: "Open Sans", sans-serif;
                font-size: 22px;
                font-weight: 400;
                line-height: 42px;
                text-align: center;
                display: block;
            }
            &>button {
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                width: 217px;
                height: 56px;
                background-color: #25365d;
                border-radius: 28px;
                border:0;
                color:#ffffff;
                font-size: 18px;
                font-weight: 700;
                line-height: 42px;
                transition: all ease 0.3s;
                &:hover {
                    background-color: #0b193b;
                }

                &>i {
                    width: 17px;
                    height: 17px;
                    opacity: 0.6;
                    color: #bbd8ff;
                    margin-right: 10px;
                }


            }
        }
    }

</style>


